
'use client';

import React, { createContext, useContext, useState, ReactNode, useCallback, useEffect } from 'react';
import { useRouter, usePathname } from 'next/navigation';
import { useUser } from '@/firebase';
import { initiateGoogleSignIn } from '@/firebase/non-blocking-login';
import { getAuth, signOut } from 'firebase/auth';

const ADMIN_EMAIL = 'admin@example.com'; 

interface AuthContextType {
  isLoggedIn: boolean;
  isAdmin: boolean;
  isUserLoading: boolean;
  MOCK_VOLUNTEER_ID: string;
  signInWithGoogle: () => void;
  signInAsAdmin: () => void;
  signInAsVolunteer: () => void;
  logout: () => void;
  toggleAdmin: () => void;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function AuthProvider({ children }: { children: ReactNode }) {
  const { user: firebaseUser, isUserLoading } = useUser();
  const [mockIsLoggedIn, setMockIsLoggedIn] = useState(false);
  const [mockIsAdmin, setMockIsAdmin] = useState(false);
  const router = useRouter();

  useEffect(() => {
    // This effect syncs mock state from session storage on initial load
    if (typeof window !== 'undefined' && !firebaseUser && !isUserLoading) {
        const loggedInStatus = sessionStorage.getItem('isLoggedIn') === 'true';
        const adminStatus = sessionStorage.getItem('isAdmin') === 'true';
        setMockIsLoggedIn(loggedInStatus);
        setMockIsAdmin(adminStatus);
    }
  }, [firebaseUser, isUserLoading]);
  
  // Determine final auth state
  const isLoggedIn = !!firebaseUser || mockIsLoggedIn;
  const isAdmin = firebaseUser ? firebaseUser.email === ADMIN_EMAIL : mockIsAdmin;

  const signInWithGoogle = useCallback(() => {
    initiateGoogleSignIn(getAuth());
  }, []);

  const signInAsAdmin = useCallback(() => {
    sessionStorage.setItem('isLoggedIn', 'true');
    sessionStorage.setItem('isAdmin', 'true');
    setMockIsLoggedIn(true);
    setMockIsAdmin(true);
    router.push('/admin');
  }, [router]);

  const signInAsVolunteer = useCallback(() => {
    sessionStorage.setItem('isLoggedIn', 'true');
    sessionStorage.setItem('isAdmin', 'false');
    setMockIsLoggedIn(true);
    setMockIsAdmin(false);
    router.push('/my-schedule');
  }, [router]);

  const logout = useCallback(async () => {
    // Clear both real auth and mock auth state
    try {
        await signOut(getAuth());
    } catch (error) {
        console.error("Error signing out from Firebase:", error);
    }
    sessionStorage.removeItem('isLoggedIn');
    sessionStorage.removeItem('isAdmin');
    setMockIsLoggedIn(false);
    setMockIsAdmin(false);
    router.push('/');
  }, [router]);
  
  const toggleAdmin = useCallback(() => {
    if (firebaseUser?.email !== ADMIN_EMAIL) {
      console.warn("Toggle is only available for the designated admin user.");
      return;
    }
    // This is a conceptual toggle; a real app would use custom claims.
    const newIsAdmin = !isAdmin;
    router.push(newIsAdmin ? '/admin' : '/my-schedule');
  }, [isAdmin, router, firebaseUser]);
  
  const value = {
    isLoggedIn,
    isAdmin,
    isUserLoading,
    signInWithGoogle,
    signInAsAdmin,
    signInAsVolunteer,
    logout,
    toggleAdmin,
    MOCK_VOLUNTEER_ID: 'user_volunteer_1',
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}

export function useAuth() {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}
